html,
body {
  padding: 0px;
  margin: 0;
  width: 100%;
  height: 100%;
  font-family: 'Fira Sans', sans-serif;
  font-weight: 400;
  font-size: 15px;
  color: white;
  overflow-x: hidden;
  -webkit-tap-highlight-color: transparent;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-font-smoothing: antialiased;
  position: relative;
}

.container {
  margin: 50px;
  padding-top: 60px;
  padding-bottom: 60px;
  background-color: #1f2682;
}

.logan {
  letter-spacing: 2.5px;
  writing-mode: vertical-rl;
  transform: rotate(180deg);
  margin-left: 50px;
  margin-right: 50px;
}

*,
::before,
::after {
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

img {
  display: block;
}

input {
  outline: none;
  border: none;
}

a {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

@font-face {
  font-family: 'Roboto';
  font-weight: 500;
  font-style: normal;
  src: url("../font/Roboto-Medium.ttf");
}

@font-face {
  font-family: 'Roboto';
  font-weight: 700;
  font-style: normal;
  src: url("../font/Roboto-Bold.ttf");
}

@font-face {
  font-family: 'Roboto';
  font-weight: 700;
  font-style: italic;
  src: url("../font/Roboto-BoldItalic.ttf");
}

@font-face {
  font-family: 'Roboto';
  font-weight: 900;
  font-style: normal;
  src: url("../font/Roboto-Black.ttf");
}

@font-face {
  font-family: 'Fira';
  font-weight: normal;
  font-style: normal;
  src: url("../font/FiraCode-Regular.ttf");
}

@font-face {
  font-family: 'Mistrain';
  font-weight: normal;
  font-style: normal;
  src: url("../font/Mistrain-4BBG9.ttf");
}

.tag {
  display: inline-block;
  padding: 3px 5px 1px 7px;
  border: none;
  border-radius: 2px;
}

.tag-blue {
  background-color: #404695;
  border: none;
  border-radius: 2px;
}

.tag-pink {
  background-color: #e83890;
  border: none;
  border-radius: 2px;
}

.font-roboto {
  font-family: 'Roboto';
}

.font-fira {
  font-family: 'Fira';
}

.font-mistrain {
  font-family: 'Mistrain';
}

.size-tiny {
  font-size: 11px;
}

.size-small {
  font-size: 13px;
}

.size-regular {
  font-size: 15px;
}

.size-normal {
  font-size: 17px;
}

.size-medium {
  font-size: 18px;
}

.size-big {
  font-size: 50px;
}

.size-huge {
  font-size: 65px;
}

.style-italic {
  font-style: italic;
}

.style-normal {
  font-weight: 400;
}

.style-medium {
  font-weight: 500;
}

.style-bold {
  font-weight: 700;
}

.style-huge {
  font-weight: 900;
}

.flex {
  display: flex;
}

.flex .space-around {
  justify-content: space-around;
}

.flex .space-between {
  justify-content: space-between;
}

.flex .center {
  align-items: center;
}

.flex-right {
  display: flex;
  justify-content: flex-end;
}

.header {
  z-index: 2;
  margin-right: 50px;
  width: 100%;
}

.header-item {
  margin-top: 15px;
  margin-left: 30px;
}

.header-item img {
  width: 15px;
}

.row {
  z-index: 2;
}

.banner {
  margin-left: 200px;
}

.banner-tags-item {
  margin-right: 10px;
  padding-left: 6px;
  padding-right: 6px;
  height: 25px;
}

.banner-title {
  letter-spacing: 1px;
  line-height: 1.25;
  padding-top: 12px;
}

.banner-title:hover {
  text-decoration: underline #e83890;
}

.banner-content {
  letter-spacing: 1px;
  line-height: 1.25;
  padding-top: 25px;
}

.banner-content svg {
  width: 18px;
  fill: white;
  display: inline;
}

.banner-content p {
  padding-left: 15px;
  padding-top: 15px;
}

.banner-left {
  position: relative;
  top: -120px;
  z-index: 3;
  width: 610px;
}

.banner-middle {
  z-index: 0;
  position: absolute;
  top: 25px;
  right: 180px;
}

.banner-middle .game {
  z-index: 1;
  width: 200px;
  height: 160px;
  position: absolute;
  top: 220px;
  left: 245px;
  overflow: hidden;
}

.banner-middle .game-item {
  position: relative;
  top: -60px;
  width: 33.33%;
}

.banner-middle .game-item img {
  width: 80%;
}

.banner-middle .game #html {
  animation-name: game;
  animation-duration: 6s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-out;
}

.banner-middle .game #css {
  animation-name: game;
  animation-duration: 6s;
  animation-delay: 2s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-out;
}

.banner-middle .game #js {
  animation-name: game;
  animation-duration: 6s;
  animation-delay: 4s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-out;
}

@keyframes game {
  from {
    top: -60px;
  }

  to {
    top: 150px;
  }
}

.banner-middle img {
  width: 730px;
  filter: brightness(0.85);
}

.featured {
  margin-left: 130px;
  margin-right: 130px;
}

.featured-banner img {
  top: 250px;
  width: 418px;
}

.featured-row {
  margin-bottom: 100px;
  width: calc(50% - 40px);
}

.featured .row-right {
  margin-left: 50%;
}

.featured-title {
  margin-top: -250px;
  letter-spacing: 1px;
  line-height: 1.5;
  margin-left: 40px;
}

.featured-title:hover {
  text-decoration: underline #e83890;
}

.featured-content {
  letter-spacing: 1px;
  line-height: 1.5rem;
  margin-left: 40px;
}

.featured-content svg {
  width: 10px;
  fill: white;
  display: inline;
}

.featured-content p {
  display: inline-block;
  width: 90%;
  padding-left: 15px;
  padding-top: 15px;
}

.featured-status {
  margin-left: 40px;
}

.featured-tag {
  margin-left: 40px;
  margin-top: 30px;
}

.featured-tag-item {
  margin-right: 10px;
  padding-left: 6px;
  padding-right: 6px;
  height: 25px;
}